// 
export const formatter = (type, value) =>{
			                if (type === 'year') {
			                    return `${value}`
			                }
			                if (type === 'month') {
			                    return `${value}`
			                }
			                if (type === 'day') {
			                    return `${value}`
			                }
							if(type === 'hour') {
								return `${value}`
							}
							if(type === 'minute') {
								return `${value}`
							}
			                return value
			            };
						
						// 
					export const formatTime = (date)=> {
							date = new Date(date);
							let Y = date.getFullYear();
							
							let M = date.getMonth()+1;
							let D = date.getDate();
							let h = date.getHours();
							let m = date.getMinutes();
							console.log('',Y);
							return `${Y}${M}${D} ${h}${m}`;
						};