// ----------------------------------------------------
export const $deleteList = (id)=>{
	let url = `/1.1/classes/lists/${id}`;
	return $delete(url);
}
// -------------------------------------------------------

export const $editlist = (obj)=>{
	let url = `/1.1/classes/lists/${obj.objectId}`;
	return $put(url,obj);
}

// 
// 
export const $editFinish = (id,obj)=>{
	let url = `/1.1/classes/lists/${id}`;
	return $put(url,obj);
}


